import React from 'react';

import Button from './Button';

export default function SideMenu({ loading, buttons })
{
 
  if (loading) {
    return <div className="side-menu">loading</div>;
  }

  if (buttons.length === 0) {
    return <div className="side-menu">empty</div>;
  }

  return (
    <div className="side-menu">
      {buttons.map((button,index) => (<>

  <Button key={button.id} button={button} />
        </>
      ))}
    </div>
  );
}